/* FONTS */
@import url("https://fonts.googleapis.com/css?family=Open+Sans:400,700&display=swap");
@import url("https://fonts.googleapis.com/css?family=Schoolbell&display=swap");

/* SETUP */
article,aside,details,figcaption,figure,footer,header,hgroup,nav,section,summary{display:block;}audio,canvas,video{display:inline-block;}audio:not([controls]){display:none;height:0;}[hidden]{display:none;}html{font-family:sans-serif;-webkit-text-size-adjust:100%;-ms-text-size-adjust:100%;}a:focus{outline:thin dotted;}a:active,a:hover{outline:0;}h1{font-size:2em;}abbr[title]{border-bottom:1px dotted;}b,strong{font-weight:700;}dfn{font-style:italic;}mark{background:#ff0;color:#000;}code,kbd,pre,samp{font-family:monospace, serif;font-size:1em;}pre{white-space:pre-wrap;word-wrap:break-word;}q{quotes:\201C \201D \2018 \2019;}small{font-size:80%;}sub,sup{font-size:75%;line-height:0;position:relative;vertical-align:baseline;}sup{top:-.5em;}sub{bottom:-.25em;}img{border:0;}svg:not(:root){overflow:hidden;}fieldset{border:1px solid silver;margin:0 2px;padding:.35em .625em .75em;}button,input,select,textarea{font-family:inherit;font-size:100%;margin:0;}button,input{line-height:normal;}button,html input[type=button],/* 1 */input[type=reset],input[type=submit]{-webkit-appearance:button;cursor:pointer;}button[disabled],input[disabled]{cursor:default;}input[type=checkbox],input[type=radio]{box-sizing:border-box;padding:0;}input[type=search]{-webkit-appearance:textfield;-moz-box-sizing:content-box;-webkit-box-sizing:content-box;box-sizing:content-box;}input[type=search]::-webkit-search-cancel-button,input[type=search]::-webkit-search-decoration{-webkit-appearance:none;}textarea{overflow:auto;vertical-align:top;}table{border-collapse:collapse;border-spacing:0;}body,figure{margin:0;}legend,button::-moz-focus-inner,input::-moz-focus-inner{border:0;padding:0;}

* { -moz-box-sizing: border-box; -webkit-box-sizing: border-box; box-sizing: border-box; }

.visuallyHidden:not(:focus):not(:active) { position: absolute; width: 1px; height: 1px; margin: -1px;border: 0;padding: 0;white-space: nowrap;clip-path: inset(100%);clip: rect(00 0 0);overflow: hidden;}

html{
	font-size: 16px;
}

ul{
	list-style: none;
	padding: 0;
}

a{
	text-decoration: none;
}

/* FOCUS */
*:focus,
a:focus { 
	outline: 2px dotted  #a12312 ;
}

/* BACKGROUND */
body{
	background: url("/greyBG.png");
}

/* HEADER */
h1{
	margin: 10px auto;
}

header{
	align-items: center;
	display: flex;
	height: auto;
	justify-content: center;
	flex-direction: column;
	width: 100%;
}

.headerIntro,
.headerIntro p{
	margin: 0;
	padding: 0 4%;
}

.headerIntro p{
	font-family: 'Open Sans', sans-serif;
}

.skipLinks{
	display: flex;
	flex-direction: column;
	justify-content: space-around;	
}
.skipLinks a{
	border: 2px inset gray;
	color: #ffffff;
	font-family: 'Open Sans', sans-serif;
	font-weight: 700;
	border-radius: 5px;
	display: flex;
	justify-content: center;
	padding:5px;
	margin: 10px auto;
	height: 50px;
	align-items: center;
	text-shadow: 1px 1px 0.5px #808080;
	text-transform: capitalize;
}

/* GROCERY BUTTONS */

.groceryList {
	margin: 20px;
}

.groceryList li{
	align-items: center;
	display: flex;
	justify-content: space-between;
	font-family: 'Schoolbell', cursive;
	font-size: 1.5rem;
	padding: 1px;
	width: 100%;
	text-indent: 50px;

}

button{
	color: #010203;
	font-family: 'Open Sans', sans-serif;
	font-weight: 700;
	border-radius: 5px;
	display: flex;
	justify-content: center;
	align-items: center;
	text-shadow: 1px 1px 0.5px #808080;
	text-transform: capitalize;
	}

.vegetableList button{
	background: #a4ffaa;

	}
.fruitList button{
	background: #ffa395;
}

.proteinList button{
	background: rgb(255, 223, 135);

}

/* GROCERY LIST */

.list {
	display: flex;
	flex-wrap: wrap;
}

.paper {
	min-width: 300px;
	min-height: 500px;
	position: relative;
	margin: 10px ;
	padding-top: 50px;
	padding-bottom: 20px;
	background-color: white;
	box-shadow: 0px 0px 5px 0px #888;
}


.pattern {
	background-color: #fff;
	background-image: linear-gradient(#eee .1em, transparent .1em);
	background-size: 100% 1.2em;
}


@media (min-width: 767px){

	
	#backToTop,
	#getList1,
	#top1,
	#top2,
	#top3
	{
		display: none;
	}

	.App{
		display: flex;
		flex-direction: column
	}

	.skipLinks{	
		flex-direction: row;
	}

	.skipLinks a{
		font-size: 1.25rem;
		margin: 5px 10px;
		padding: 5 10px;
	}

	.appScreen {
		background: linear-gradient(-45deg, lightgrey 0%, rgb(228, 228, 228) 20.5%, lightgrey 31%, rgb(230, 230, 230) 55%);
		box-shadow: 0 0 0 20px black;
		border-bottom: 30px solid black;
		border-radius: 20px;
		margin: 3% 5%;
		padding: 2%;
	}

	.contentSection{
		display: flex;
	}

	.appScreen,
	.listScreen{
		height: auto;
		width: 50%;
	}

	.listScreen{
		margin: 1% 0;
		overflow-y: scroll;
		max-height: 650px;
	}

	

}


@media (min-width: 320px){

	h1 img{
		margin: 3% auto;
		width: 275px;
	}

	.paper::before {
		content: '';
		width: 1px;
		height: 100%;
		position: absolute;
		top: 0;
		left: 60px;
		background-color: #ff000099;
	}


	.groceryButton{ 
		margin: 10px;
		height: 50px;
		flex-shrink: 0;
		width: calc(33.33% - 20px);
	}


	.appScreen {
		height: 100vh;
		max-height: 600px;
	}

	.getList{
		background-color: #1a9d49;
		border: inset #808080 3px;
		border-radius: 10px;
		color: #ffffff;
		display: block;
		font-family: 'Open Sans', sans-serif;
		font-size: 1rem;
		font-weight: 700;
		margin: 10px auto;
		padding: 5px ;
		text-align: center;
		text-decoration: none;
		width: 60%;

	}

	.removeItem{
		background-color: transparent;
		border: none;
		color:  #a12312 ;
		font-size: 1.5rem;
	}	

	.listScreen{
		overflow-y: scroll;
	}
	
}


